import React, { Suspense } from "react";
import { HomeContainer, Heading, HeaderImg, LightText, GrayText } from "../CommonStyles/styles";
import Header from "../Header";
import profileImg from "../../assets/img/profile.jpg";
import Skills from "../Skills";
import About from "../About";
import Tech from "../Tech";
import Experience from "../Experience";

const Home = () => {
  return (
    <HomeContainer>
      <div className="container-fluid">
        <div
          className="d-flex justify-content-center align-items-center"
          style={{ height: "80vh" }}
        >
          <div className="row">
            <div className="col-lg-6 col-xs-12">
              <div className="d-flex p-2 justify-content-around align-items-center">
                <div className="f1 flex-grow-10">
                  <HeaderImg>
                    <img src={profileImg} className="rounded-circle" />
                  </HeaderImg>
                </div>
              </div>
            </div>
            <div className="col-lg-6 col-xs-12">
              <div className="d-flex p-2 justify-content-around align-items-center">
                <div className="f1 flex-grow-10">
                  <Heading size={3.5}>Hello, I'm Saravanan</Heading>
                  <LightText className="lead">
                    Full-Stack Developer and Project Lead <br /> <br />
                    <a
                      download
                      class="btn btn-primary"
                      href="https://drive.google.com/file/d/1Y0PcsbWJYlrhPfqabG-_u8XRQrxpmFtK/view?usp=sharing"
                      role="button"
                      target="_blank"
                    >
                      View Resume
                    </a>
                  </LightText>
                </div>
              </div>
            </div>
          </div>
        </div>
        <Suspense fallback={<div className="text-center">Loading...</div>}>
          <Skills /> <hr></hr>
        </Suspense>
        <Suspense fallback={<div className="text-center">Loading...</div>}>
          <About />
        </Suspense>
        <Suspense fallback={<div className="text-center">Loading...</div>}>
          <Experience />
        </Suspense>
        <Suspense fallback={<div className="text-center">Loading...</div>}>
          <Tech />
        </Suspense>
      </div>
    </HomeContainer>
  );
};

export default Home;
